*{
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

.header{
    width: 100%;
    background-color: cornflowerblue;
    margin-bottom: 100px;
}

.menu__list{
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
}

.menu__item{
    padding: 0 30px;
}
.menu__link{
    font-size: 30px;
    color: #ffffff;
    text-decoration: none;
}

.menu__link:hover{
    text-decoration: underline;
}